import {
    number_format
} from '../../components/my-components/formater.js'
export default {
    data() {
        return {
            menuIndex: '',
        }
    },
    props: {
        Jurisdiction: {
            type: Object,
            default: {},
            required: true
        },
        toggle: {
            type: Boolean,
            default: true
        },
        vipsList: {
            type: Array,
            default: [{
                img: '/static/img/global/home_order_tobepay.png',
                title: '',
                vipClass: '会员',
                tel: 1524516566,
                money: 0,
                score: 0
            }]
        }
    },
    watch: {
        toggle() {
            this.menuIndex = ''
        }
    },
    methods: {
        showMenu(vipindex) {
            this.menuIndex = 'vip-' + vipindex;
        },
        clickItem(vipindex, item) {
            this.$emit('click', {
                type: 'item',
                detail: Object.assign({
                    vipIndex: vipindex,
                }, item)
            })
        },
        clickMenuItem(vipindex, item, name) {
            this.menuIndex = '';
            this.$emit('click', {
                type: 'menu-item',
                name: name,
                detail: Object.assign({
                    vipIndex: vipindex,
                },item)
            })
        },
        formatePrice(val) {
            return number_format(val, 2, '.', ',')
        }
    }
}
